$space: 5vmax;
$space-multiplicator: 1;

$space-xxs: calc((#{$space} / 8) * #{$space-multiplicator});
$space-xs: calc((#{$space} / 4) * #{$space-multiplicator});
$space-s: calc((#{$space} / 2) * #{$space-multiplicator});
$space-m: calc(#{$space} * #{$space-multiplicator});
$space-l: calc((#{$space} * 2) * #{$space-multiplicator});
$space-xl: calc((#{$space} * 4) * #{$space-multiplicator});
$space-xxl: calc((#{$space} * 8) * #{$space-multiplicator});

// xxs | space-xxs | .25x
// xs  | space-xs  | .5x
// s   | space-s   | .75x
// m   | space     | 1x
// l   | space-l   | 1.5x
// xl  | space-xl  | 2x
// xxl | space-xxl | 4x

